<template>
  <div>

        <b-navbar type="dark" variant="primary" :sticky=true>
        <b-navbar-nav>
          <b-nav-form>
            <b-button variant="primary" disabled>
              <b-icon icon="list"></b-icon>
            </b-button>
          </b-nav-form>
          <b-navbar-brand>
          Shopping List
          </b-navbar-brand>
        </b-navbar-nav>
        </b-navbar>


      <b-container>




      <b-row>
        <b-col></b-col>
        <b-col cols="12" md="6">
        
        <div class="my-3">{{purchaseCount}} out of {{shoppingList.length}}</div>
        
        <b-tabs content-class="mt-3" pills>
        <b-tab title="Checklist">
        <ListRow
          v-for="item,index in shoppingList" 
          :key="index"
          :item="item.name"
          :count="item.count"
          :purchased ="item.purchased"
          @toggleSelected="toggleSelected($event)"
        />
        </b-tab>
        <b-tab title="Text Mode" lazy>
          <b-textarea
          :value = "shoppingListText"
          rows="8"
          id="text-list" 
          >
          </b-textarea>
          <b-button class="mt-3" variant="outline-secondary" @click="copytoClipBoard">
            Copy to clipboard
          </b-button>
        </b-tab>
        </b-tabs>

        </b-col>
        <b-col></b-col>
      </b-row>

      <b-row>
        <b-col></b-col>
        <b-col cols="12" md="6" style="position:relative">
      
      <TabBar
        :index = 1
        :shoppingListCount = "shoppingListCount"
        :keyBoardVisible = "keyBoardVisible"
      >
      </TabBar>

    </b-col>
        <b-col></b-col>

      </b-row>

      <!-- Alerts -->
            <b-alert 
            :show="countDownAlert"
            variant="success"
            class="alert-class"
            @dismissed="countDownAlert=0"
            fade
            >
            Copied!
            </b-alert>  
      <!-- End of Alerts -->

      </b-container>
    

  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import { mapState, mapMutations } from 'vuex'
import ListRow from "@/components/ListRow.vue"
import TabBar from "@/components/TabBar.vue"

let windowHeight = 0;

export default {
  name: 'ShoppingList',
  components: {
    ListRow,
    TabBar,
  },
  data(){
    return{
      itemsFound:0,
      keyBoardVisible:false,
      countDownAlert:0,
      dismissSecs:1
    }
  },
  methods:{
    ...mapMutations([
      'toggleSelected'
    ]),
    copytoClipBoard() {
      this.countDownAlert = this.dismissSecs
      navigator.clipboard.writeText(this.shoppingListText)
      
    },
  },
  beforeMount(){
      //Code to decide if buttons need hiding
  if(windowHeight == 0){
        windowHeight = window.innerHeight
    }
    window.addEventListener("resize", ()=>{
    

      if(window.innerHeight < windowHeight){
        this.keyBoardVisible = true
      }else{
        this.keyBoardVisible = false
      }
    })

  },
  computed:{
    shoppingList(){

      let a = this.pantry_list
              .map(section=>section.items)
              .flat()
              .filter(item=>item.count > 0)


      return a
    },
    shoppingListText(){

      let a = this.pantry_list
              .map(section=>section.items)
              .flat()
              .filter(item=>item.count > 0)
              .filter(item=>item.purchased==false)
      
      let textArray = a.map(item=>item.name + (item.count > 1 ? " (" + item.count + ")" : ""))
      
      return textArray.join(",\n")
    },


    purchaseCount(){

        let a = this.shoppingList
                .filter(item=>item.purchased === true)
        return a.length
    },
    shoppingListCount(){

      let a = this.pantry_list
              .map(section=>section.items)
              .flat()
              .filter(item=>item.count > 0)

      return a.length
    },
    ...mapState([
      'pantry_list'
    ])
  }
}
</script>


<style>

/* *{
  border:1pt solid red;
} */

  .alert-class{
    position:fixed;
    bottom:13rem;
    left:50%;
    transform:translateX(-50%);
    width:10rem;
  }




